body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #6c5ce7;
}

.parent {
  background: #fff;
  padding: 30px;
  box-shadow: 1px 1px 20px;
}

.container {
  display: -webkit-flex;
  display: flex;
  height: 100vh;
  -webkit-align-items: center;
          align-items: center;
}

.parent h3 {
  color: #2d3436;
}

.title {
  border: 2px dashed #bbb;
  padding: 55px 0;
  background: #eee;
}

.fa-image {
  font-size: 38px;
  color: #bbb;
}

.size {
  margin-left: 15px;
}

.pull-right {
  float: right;
}

.details {
  font-size: 13px;
}

.loader {
  padding: 0;
}

.padding-left {
  padding-left: 15px;
}

.padding-right {
  padding-right: 15px;
}

.loader hr {
  border-width: 5px;
  margin: 5px 0;
}

.progressbar hr {
  border-color: #0984e3;
}

.file-container {
  padding-top: 10px;
}
